// main: ../style.less


.input {
    display: block;
    width: 100%;

    padding: 1.4rem 1.8rem;

    border: 1px solid fade(#707070, 30%);
    border-radius: 1rem;

    font-family: inherit;
    font-size: .8rem;
    line-height: 1.2;
    font-weight: 400;

    transition:  border-color .2s linear;

    &:focus {
        outline: none;
        border-color: #36abc7;
    }
}

